import React, { useState } from 'react';
import { ListItemText , ListItem, ListItemIcon, ListItemSecondaryAction, IconButton, Checkbox, Input, Grid } from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/Delete';
import { useDispatch } from 'react-redux';
import { deleteTask, changeDoneStatus } from './taskSlice';
import {changeTaskName} from './taskSlice';



export const ToDoListItem = ({task}: any) => {
  const dispatch = useDispatch();
  const [changeInput, setChangeInput] = useState(false);
  const [disab, setDisab] = useState(false);
  const showInput = () => {
    setChangeInput(!changeInput)
  }; 
  const buttonDisabled = () => {
    setDisab(true)
  };

  const changeTask = (e: React.KeyboardEvent<HTMLDivElement> | 
    React.ChangeEvent<HTMLInputElement>) =>  { 
      const {key} = (e as React.KeyboardEvent<HTMLDivElement>);
      const {target} = (e as React.ChangeEvent<HTMLInputElement>);
      if (key === 'Enter' && target.value.trim()){
        dispatch(changeTaskName({id: target.name, name: target.value}))
      }
  };
  
const date = task.createdAt.match(/([12]\d{3}-(0[1-9]|1[0-2])-(0[1-9]|[12]\d|3[01]))/)[0];
const time = task.createdAt.match(/T(((([0-1][0-9])|(2[0-3])):?[0-5][0-9]))/g)[0].replace('T', '');
 return(
   <Grid container>
    <ListItem>
      <Grid item xs={1}>
      <ListItemIcon>
        <Checkbox 
          color="primary"  
          onChange={(e)=> dispatch(changeDoneStatus({id: e.target.value, done: e.target.checked}))} 
          checked={task.done} 
          value={task.id} 
        />
      </ListItemIcon>
      </Grid>
      <Grid item xs={8}>
      {changeInput 
        ? (<Input
            fullWidth
            defaultValue={task.name}
            autoFocus={true}
            name={task.id}
            onBlur={() => showInput()} 
            onKeyDown={(e: React.KeyboardEvent<HTMLInputElement>) => e.key === "Escape" && setChangeInput(false)} 
            onKeyPress={e => changeTask(e)}
            onKeyUp={(e: React.KeyboardEvent<HTMLInputElement>) => e.key === "Enter" &&  setChangeInput(false)}
          />) 
        : (<ListItemText  
            primary={task.name}  
            onClick={() => showInput()}
          />)
      }
      </Grid>
        <Grid item xs={2}>
        <ListItemText  primary={`${time} ${date}`} />
        </Grid>
      <Grid item xs={1}>
        <ListItemSecondaryAction>
                    <IconButton 
                      edge="end" 
                      disabled={disab} 
                      aria-label="delete" 
                      onClick={(e) => {dispatch(deleteTask(e.currentTarget.value)); buttonDisabled()}} 
                      value={task.id}>
                      <DeleteIcon />
                    </IconButton>
        </ListItemSecondaryAction>
        </Grid>
        </ListItem>
   </Grid>
      
 )   
};